export default {
	props:{
		item:Object,
		index:Number
	},
	data() {
		return {
			isguanzhu: this.item.isguanzhu,
			infonum: this.item.infonum,
		}
	},
	methods:{
		//关注的处理
		guanzhu(){
			  
				if(!this.item.isguanzhu){
					uni.showToast({
						title:'关注成功',
					});
				}else{
					uni.showToast({
						title:'取消关注成功',
					});
				}
		this.isguanzhu= !this.isguanzhu;	
		},
		//顶踩
					caozuo(type){
						switch (type){
							case "ding":
							if(this.infonum.index==1){ return; }
							this.infonum.ding++;
							if(this.infonum.index==2){
								this.infonum.cai--;
							}
							this.infonum.index=1;
								break;
							case "cai":
							if(this.infonum.index==2){ return; }
							this.infonum.cai++;
							if(this.infonum.index==1){
								this.infonum.ding--;
							}
							this.infonum.index=2;
								break;
						}
						
		},
		//进入详情页
			opendetail(){
		uni.showModal({
			content: '进入详情页',
			showCancel: false
		});
			}
	},

	
}
